import "./Skills.scss"
import * as React from 'react';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';

import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
  
const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
    height: 10,
    borderRadius: 5,
    width: '50%',
    [`&.${linearProgressClasses.colorPrimary}`]: {
      backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
    },
    [`& .${linearProgressClasses.bar}`]: {
      borderRadius: 5,
      backgroundColor: theme.palette.mode === 'light' ? '#1a90ff' : '#308fe8',
    },
}));




export default function Skills() {
    return (
        <div className='skills' id="skills" style={{backgroundImage: `url("assets/ITLB_B_BG-2.png")`}}>
            <h1 className="h1">My Skills : </h1>
            <br></br>
            <br></br>

            <Grid container rowSpacing={6} className='grid' columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
              <Grid item xs={6}>
                <h2 className="h2">Javascript : 90%</h2>
                <BorderLinearProgress variant="determinate" value={90} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">HTML / CSS : 70%</h2>
                <BorderLinearProgress variant="determinate" value={70} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">SQL : 80%</h2>
                <BorderLinearProgress variant="determinate" value={80} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">English : 85%</h2>
                <BorderLinearProgress variant="determinate" value={85} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">Dutch : 60%</h2>
                <BorderLinearProgress variant="determinate" value={60} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">French : 95%</h2>
                <BorderLinearProgress variant="determinate" value={95} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">Node Js : 80%</h2>
                <BorderLinearProgress variant="determinate" value={80} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">Symphony</h2>
                <BorderLinearProgress variant="determinate" value={75} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">Gitlab / Github : 80%</h2>
                <BorderLinearProgress variant="determinate" value={80} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">React / Native : 80%</h2>
                <BorderLinearProgress variant="determinate" value={80} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">Python : 85%</h2>
                <BorderLinearProgress variant="determinate" value={85} />
              </Grid>
              <Grid item xs={6}>
                <h2 className="h2">Design : 30%</h2>
                <BorderLinearProgress variant="determinate" value={30} />
              </Grid>
            </Grid>
        </div>
    )

}